/* tabs */

/* tabs adaption */
window.addEventListener('load', tabsOverflow);
window.addEventListener('resize', tabsOverflow);

function tabsOverflow() {
  let outer = document.querySelector('.tabs-container');
  let inner = document.querySelector('.tabs');
  if (inner.offsetWidth > outer.offsetWidth) {
    inner.classList.add('overflow');
  } else {
    inner.classList.remove('overflow');
  }
};

/* tabs working */
const tabs = document.querySelectorAll('.tabs__item');
const targets = document.querySelectorAll('.menu__slider-block');

tabs.forEach((tab, tabIndex) => {
  tab.addEventListener('click', () => {
    tabs.forEach((tab) => {
      tab.classList.remove('active');
    });
    tab.classList.add('active');

    targets.forEach((target, targetIndex) => {
      target.classList.remove('show');
      targetIndex = tabIndex;
      targets[targetIndex].classList.add('show');
    });
  });
});

/* tabs slider */
const swiperMenu = new Swiper('.menu__slider', {
  speed: 500,
  grabCursor:true,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  slidesPerView: 1,
  grid: {
    rows: 1,
  },
  breakpoints: {
    550: {
      slidesPerView: 2,
      grid: {
        rows: 1
      },
    },
    820: {
      slidesPerView: 3,
      grid: {
        rows: 1
      },
    },
    1000: {
      slidesPerView: 2,
      grid: {
        rows: 2
      },
    },
  },
});