/* show popup */

showPopups();
function showPopups() {
  document.addEventListener('scroll', () => {
    const popups = document.querySelectorAll('.phone-popup');
      function showPopup() {
        popups.forEach((popup) =>{
          let windowCenter = (window.innerHeight) + window.scrollY;
          let scrollOffSet = (popup.closest('section').offsetTop) + (popup.closest('section').offsetHeight);
          if (windowCenter >= scrollOffSet) {
            popup.classList.add('show-popup');
          } else {
            popup.classList.remove('show-popup');
          }
        });
      }
    showPopup();
  }); 
};/* end of showPopups */

